body {
    font-family: 'Poppins', sans-serif;
    background: linear-gradient(135deg, #f0f8ff, #e6e6fa); /* Soft gradient */
    margin: 0;
    padding: 0;
    color: #333;
  }
  
  .session-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    background: #fff;
    border-radius: 12px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  }
  
  .session-header {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .session-header h1 {
    color: #4caf50; /* Positive green */
    margin-bottom: 10px;
  }
  
  .role-message {
    font-size: 1.1em;
    color: #555;
  }
  
  .session-main {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 20px;
  }
  
  .document-canvas-container, .audio-chat-container {
    flex: 1;
    padding: 15px;
    border-radius: 10px;
    background: #f9f9f9;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  }
  
  h2 {
    font-size: 1.5em;
    color: #4caf50;
    margin-bottom: 10px;
  }
  
  .session-footer {
    text-align: center;
  }
  
  .quiz-link {
    display: inline-block;
    padding: 12px 20px;
    background-color: #4caf50;
    color: white;
    text-decoration: none;
    border-radius: 8px;
    font-size: 1.1em;
    transition: transform 0.2s ease, background-color 0.3s ease;
  }
  
  .quiz-link:hover {
    background-color: #3e8e41; /* Darker green */
    transform: scale(1.05);
  }
  
  .error-message {
    color: red;
    text-align: center;
    font-weight: bold;
  }
  